import isMobile from "../../../utils/isMobile";

import Pointer from "./Pointer";
import Material from "./Material";
import Program from "./Program";
import {
  normalizeColor,
  HSVtoRGB,
  easeInOutQuart,
  getWebGLContext,
  compileShader,
  scaleByPixelRatio,
  createTextureAsync,
  getRandomValue,
} from "./helpers";
import * as shaders from "./shaders";

export default class FluidSimulation {
  constructor(canvas, config) {
    const { gl, ext } = getWebGLContext(canvas);

    this.el = canvas;
    this.gl = gl;
    this.ext = ext;
    this.config = config;
    this.paused = false;

    this.pointers = [];
    this.splatStack = [];

    this.init();
  }

  init() {
    const { gl, ext } = this;

    this.pointers.push(new Pointer());

    if (isMobile()) {
      this.config.DYE_RESOLUTION = 512;
    }

    if (!ext.supportLinearFiltering) {
      this.config.DYE_RESOLUTION = 512;
      this.config.SHADING = false;
      this.config.BLOOM = false;
      this.config.SUNRAYS = false;
    }

    this.dye = null;
    this.velocity = null;
    this.divergence = null;
    this.curl = null;
    this.pressure = null;
    let bloom;
    let bloomFramebuffers = [];
    let sunrays;
    let sunraysTemp;

    let colorUpdateTimer = 0.0;
    let noiseUpdateTimer = 0.0;

    let lastUpdate = 0.0;
    let delta = 0.0;

    let ditheringTexture = createTextureAsync("/LDR_LLL1_0.png", this.gl);

    // Compile shaders
    const blurVertexShader = compileShader(
      gl.VERTEX_SHADER,
      shaders.blurVertexShaderSrc,
      gl
    );
    const baseVertexShader = compileShader(
      gl.VERTEX_SHADER,
      shaders.baseVertexShaderSrc,
      gl
    );
    const colorShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.colorShaderSrc,
      gl
    );
    const copyShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.copyShaderSrc,
      gl
    );
    const blurShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.blurShaderSrc,
      gl
    );
    const clearShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.clearShaderSrc,
      gl
    );
    const bloomPrefilterShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.bloomPrefilterShaderSrc,
      gl
    );
    const bloomBlurShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.bloomBlurShaderSrc,
      gl
    );
    const bloomFinalShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.bloomFinalShaderSrc,
      gl
    );
    const sunraysMaskShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.sunraysMaskShaderSrc,
      gl
    );
    const sunraysShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.sunraysShaderSrc,
      gl
    );
    const splatShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.splatShaderSrc,
      gl
    );
    // const starfieldShader = compileShader(
    //   gl.FRAGMENT_SHADER,
    //   shaders.starfieldShaderSrc,
    //   gl
    // );
    const advectionShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.advectionShaderSrc,
      gl,
      ext.supportLinearFiltering ? null : ["MANUAL_FILTERING"]
    );
    const divergenceShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.divergenceShaderSrc,
      gl
    );
    const curlShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.curlShaderSrc,
      gl
    );
    const vorticityShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.vorticityShaderSrc,
      gl
    );
    const pressureShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.pressureShaderSrc,
      gl
    );
    const gradientSubtractShader = compileShader(
      gl.FRAGMENT_SHADER,
      shaders.gradientSubtractShaderSrc,
      gl
    );
    const blurProgram = new Program(blurVertexShader, blurShader, gl);
    const copyProgram = new Program(baseVertexShader, copyShader, gl);
    const clearProgram = new Program(baseVertexShader, clearShader, gl);
    const colorProgram = new Program(baseVertexShader, colorShader, gl);
    const bloomPrefilterProgram = new Program(
      baseVertexShader,
      bloomPrefilterShader,
      gl
    );
    const bloomBlurProgram = new Program(baseVertexShader, bloomBlurShader, gl);
    const bloomFinalProgram = new Program(
      baseVertexShader,
      bloomFinalShader,
      gl
    );
    const sunraysMaskProgram = new Program(
      baseVertexShader,
      sunraysMaskShader,
      gl
    );
    const sunraysProgram = new Program(baseVertexShader, sunraysShader, gl);
    const splatProgram = new Program(baseVertexShader, splatShader, gl);
    // const starfieldProgram = new Program(baseVertexShader, starfieldShader, gl);
    const advectionProgram = new Program(baseVertexShader, advectionShader, gl);
    const divergenceProgram = new Program(
      baseVertexShader,
      divergenceShader,
      gl
    );
    const curlProgram = new Program(baseVertexShader, curlShader, gl);
    const vorticityProgram = new Program(baseVertexShader, vorticityShader, gl);
    const pressureProgram = new Program(baseVertexShader, pressureShader, gl);
    const gradienSubtractProgram = new Program(
      baseVertexShader,
      gradientSubtractShader,
      gl
    );
    const displayMaterial = new Material(
      baseVertexShader,
      shaders.displayShaderSrc,
      gl
    );

    // Update keywords
    let displayKeywords = [];
    if (this.config.SHADING) displayKeywords.push("SHADING");
    if (this.config.BLOOM) displayKeywords.push("BLOOM");
    if (this.config.SUNRAYS) displayKeywords.push("SUNRAYS");

    displayMaterial.setKeywords(displayKeywords);

    const blit = (() => {
      gl.bindBuffer(gl.ARRAY_BUFFER, gl.createBuffer());
      gl.bufferData(
        gl.ARRAY_BUFFER,
        new Float32Array([-1, -1, -1, 1, 1, 1, 1, -1]),
        gl.STATIC_DRAW
      );
      gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, gl.createBuffer());
      gl.bufferData(
        gl.ELEMENT_ARRAY_BUFFER,
        new Uint16Array([0, 1, 2, 0, 2, 3]),
        gl.STATIC_DRAW
      );
      gl.vertexAttribPointer(0, 2, gl.FLOAT, false, 0, 0);
      gl.enableVertexAttribArray(0);

      return (target, clear = false) => {
        if (target == null) {
          gl.viewport(0, 0, gl.drawingBufferWidth, gl.drawingBufferHeight);
          gl.bindFramebuffer(gl.FRAMEBUFFER, null);
        } else {
          gl.viewport(0, 0, target.width, target.height);
          gl.bindFramebuffer(gl.FRAMEBUFFER, target.fbo);
        }
        if (clear) {
          gl.clearColor(0.0, 0.0, 0.0, 1.0);
          gl.clear(gl.COLOR_BUFFER_BIT);
        }
        gl.drawElements(gl.TRIANGLES, 6, gl.UNSIGNED_SHORT, 0);
      };
    })();

    this.initBloomFramebuffers = () => {
      let res = this.getResolution(this.config.BLOOM_RESOLUTION);

      const texType = ext.halfFloatTexType;
      const rgba = ext.formatRGBA;
      const filtering = ext.supportLinearFiltering ? gl.LINEAR : gl.NEAREST;

      bloom = this.createFBO(
        res.width,
        res.height,
        rgba.internalFormat,
        rgba.format,
        texType,
        filtering
      );

      bloomFramebuffers.length = 0;
      for (let i = 0; i < this.config.BLOOM_ITERATIONS; i++) {
        let width = res.width >> (i + 1);
        let height = res.height >> (i + 1);

        if (width < 2 || height < 2) break;

        let fbo = this.createFBO(
          width,
          height,
          rgba.internalFormat,
          rgba.format,
          texType,
          filtering
        );
        bloomFramebuffers.push(fbo);
      }
    };

    this.initSunraysFramebuffers = () => {
      let res = this.getResolution(this.config.SUNRAYS_RESOLUTION);

      const texType = ext.halfFloatTexType;
      const r = ext.formatR;
      const filtering = ext.supportLinearFiltering ? gl.LINEAR : gl.NEAREST;

      sunrays = this.createFBO(
        res.width,
        res.height,
        r.internalFormat,
        r.format,
        texType,
        filtering
      );
      sunraysTemp = this.createFBO(
        res.width,
        res.height,
        r.internalFormat,
        r.format,
        texType,
        filtering
      );
    };

    this.createFBO = (w, h, internalFormat, format, type, param) => {
      gl.activeTexture(gl.TEXTURE0);
      let texture = gl.createTexture();
      gl.bindTexture(gl.TEXTURE_2D, texture);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, param);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, param);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
      gl.texImage2D(
        gl.TEXTURE_2D,
        0,
        internalFormat,
        w,
        h,
        0,
        format,
        type,
        null
      );

      let fbo = gl.createFramebuffer();
      gl.bindFramebuffer(gl.FRAMEBUFFER, fbo);
      gl.framebufferTexture2D(
        gl.FRAMEBUFFER,
        gl.COLOR_ATTACHMENT0,
        gl.TEXTURE_2D,
        texture,
        0
      );
      gl.viewport(0, 0, w, h);
      gl.clear(gl.COLOR_BUFFER_BIT);

      let texelSizeX = 1.0 / w;
      let texelSizeY = 1.0 / h;

      return {
        texture,
        fbo,
        width: w,
        height: h,
        texelSizeX,
        texelSizeY,
        attach(id) {
          gl.activeTexture(gl.TEXTURE0 + id);
          gl.bindTexture(gl.TEXTURE_2D, texture);
          return id;
        },
      };
    };

    this.createDoubleFBO = (w, h, internalFormat, format, type, param) => {
      let fbo1 = this.createFBO(w, h, internalFormat, format, type, param);
      let fbo2 = this.createFBO(w, h, internalFormat, format, type, param);

      return {
        width: w,
        height: h,
        texelSizeX: fbo1.texelSizeX,
        texelSizeY: fbo1.texelSizeY,
        get read() {
          return fbo1;
        },
        set read(value) {
          fbo1 = value;
        },
        get write() {
          return fbo2;
        },
        set write(value) {
          fbo2 = value;
        },
        swap() {
          let temp = fbo1;
          fbo1 = fbo2;
          fbo2 = temp;
        },
      };
    };

    const resizeFBO = (target, w, h, internalFormat, format, type, param) => {
      let newFBO = this.createFBO(w, h, internalFormat, format, type, param);
      copyProgram.bind();
      gl.uniform1i(copyProgram.uniforms.uTexture, target.attach(0));
      blit(newFBO);
      return newFBO;
    };

    this.resizeDoubleFBO = (
      target,
      w,
      h,
      internalFormat,
      format,
      type,
      param
    ) => {
      if (target.width === w && target.height === h) return target;
      target.read = resizeFBO(
        target.read,
        w,
        h,
        internalFormat,
        format,
        type,
        param
      );
      target.write = this.createFBO(w, h, internalFormat, format, type, param);
      target.width = w;
      target.height = h;
      target.texelSizeX = 1.0 / w;
      target.texelSizeY = 1.0 / h;
      return target;
    };

    const update = (timestamp) => {
      if (!this.paused) {
        calcDeltaTime(timestamp);

        noiseUpdateTimer += delta / 1000;

        applyInputs();

        updateColors(delta);

        step(delta);

        if (noiseUpdateTimer < 0.002) walker(noiseUpdateTimer);

        render(null);
      }

      requestAnimationFrame(update);
    };

    const calcDeltaTime = (timestamp) => {
      delta = (timestamp - lastUpdate) / 1000;
      delta = Math.min(delta, 0.016666);
      lastUpdate = timestamp;
      return delta;
    };

    const updateColors = (dt) => {
      colorUpdateTimer += dt * this.config.COLOR_UPDATE_SPEED;

      // if (colorUpdateTimer >= 1) {
      // colorUpdateTimer = wrap(colorUpdateTimer, 0, 1);

      this.pointers.forEach((p) => {
        p.color = this.generateColor(colorUpdateTimer);
      });
      // }
    };

    const applyInputs = () => {
      this.pointers.forEach((p) => {
        if (p.moved) {
          p.moved = false;
          splatPointer(p);
        }
      });
    };

    const step = (dt) => {
      gl.disable(gl.BLEND);

      curlProgram.bind();
      gl.uniform2f(
        curlProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      gl.uniform1i(
        curlProgram.uniforms.uVelocity,
        this.velocity.read.attach(0)
      );
      blit(this.curl);

      vorticityProgram.bind();
      gl.uniform2f(
        vorticityProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      gl.uniform1i(
        vorticityProgram.uniforms.uVelocity,
        this.velocity.read.attach(0)
      );
      gl.uniform1i(vorticityProgram.uniforms.uCurl, this.curl.attach(1));
      gl.uniform1f(vorticityProgram.uniforms.curl, this.config.CURL);
      gl.uniform1f(vorticityProgram.uniforms.dt, dt);
      blit(this.velocity.write);
      this.velocity.swap();

      divergenceProgram.bind();
      gl.uniform2f(
        divergenceProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      gl.uniform1i(
        divergenceProgram.uniforms.uVelocity,
        this.velocity.read.attach(0)
      );
      blit(this.divergence);

      clearProgram.bind();
      gl.uniform1i(
        clearProgram.uniforms.uTexture,
        this.pressure.read.attach(0)
      );
      gl.uniform1f(clearProgram.uniforms.value, this.config.PRESSURE);
      blit(this.pressure.write);
      this.pressure.swap();

      pressureProgram.bind();
      gl.uniform2f(
        pressureProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      gl.uniform1i(
        pressureProgram.uniforms.uDivergence,
        this.divergence.attach(0)
      );
      for (let i = 0; i < this.config.PRESSURE_ITERATIONS; i++) {
        gl.uniform1i(
          pressureProgram.uniforms.uPressure,
          this.pressure.read.attach(1)
        );
        blit(this.pressure.write);
        this.pressure.swap();
      }

      gradienSubtractProgram.bind();
      gl.uniform2f(
        gradienSubtractProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      gl.uniform1i(
        gradienSubtractProgram.uniforms.uPressure,
        this.pressure.read.attach(0)
      );
      gl.uniform1i(
        gradienSubtractProgram.uniforms.uVelocity,
        this.velocity.read.attach(1)
      );
      blit(this.velocity.write);
      this.velocity.swap();

      advectionProgram.bind();
      gl.uniform2f(
        advectionProgram.uniforms.texelSize,
        this.velocity.texelSizeX,
        this.velocity.texelSizeY
      );
      if (!ext.supportLinearFiltering)
        gl.uniform2f(
          advectionProgram.uniforms.dyeTexelSize,
          this.velocity.texelSizeX,
          this.velocity.texelSizeY
        );

      let velocityId = this.velocity.read.attach(0);
      gl.uniform1i(advectionProgram.uniforms.uVelocity, velocityId);
      gl.uniform1i(advectionProgram.uniforms.uSource, velocityId);
      gl.uniform1f(advectionProgram.uniforms.dt, dt);
      gl.uniform1f(
        advectionProgram.uniforms.dissipation,
        this.config.VELOCITY_DISSIPATION
      );

      blit(this.velocity.write);
      this.velocity.swap();

      if (!ext.supportLinearFiltering)
        gl.uniform2f(
          advectionProgram.uniforms.dyeTexelSize,
          this.dye.texelSizeX,
          this.dye.texelSizeY
        );
      gl.uniform1i(
        advectionProgram.uniforms.uVelocity,
        this.velocity.read.attach(0)
      );
      gl.uniform1i(advectionProgram.uniforms.uSource, this.dye.read.attach(1));
      gl.uniform1f(
        advectionProgram.uniforms.dissipation,
        this.config.DENSITY_DISSIPATION
      );

      blit(this.dye.write);

      this.dye.swap();
    };

    const render = (target) => {
      if (this.config.BLOOM) applyBloom(this.dye.read, bloom);
      if (this.config.SUNRAYS) {
        applySunrays(this.dye.read, this.dye.write, sunrays);
        blur(sunrays, sunraysTemp, 1);
      }

      if (target == null || !this.config.TRANSPARENT) {
        gl.blendFunc(gl.ONE, gl.ONE_MINUS_SRC_ALPHA);
        gl.enable(gl.BLEND);
      } else {
        gl.disable(gl.BLEND);
      }

      if (!this.config.TRANSPARENT)
        drawColor(target, normalizeColor(this.config.BACK_COLOR));
      // if (target == null && this.config.TRANSPARENT) drawCheckerboard(target);

      drawDisplay(target);
    };

    const drawColor = (target, color) => {
      colorProgram.bind();
      gl.uniform4f(
        colorProgram.uniforms.color,
        color.r,
        color.g,
        color.b,
        color.a
      );

      // starfieldProgram.bind();
      // gl.uniform1f(starfieldProgram.uniforms.dt, noiseUpdateTimer * 200);
      // gl.uniform2f(
      //   starfieldProgram.uniforms.res,
      //   gl.drawingBufferWidth,
      //   gl.drawingBufferHeight
      // );

      blit(target);
    };

    const drawDisplay = (target) => {
      let width = target == null ? gl.drawingBufferWidth : target.width;
      let height = target == null ? gl.drawingBufferHeight : target.height;

      displayMaterial.bind();

      gl.uniform1f(displayMaterial.uniforms.dt, noiseUpdateTimer);

      if (this.config.SHADING)
        gl.uniform2f(
          displayMaterial.uniforms.texelSize,
          1.0 / width,
          1.0 / height
        );
      gl.uniform1i(displayMaterial.uniforms.uTexture, this.dye.read.attach(0));
      if (this.config.BLOOM) {
        gl.uniform1i(displayMaterial.uniforms.uBloom, bloom.attach(1));
        gl.uniform1i(
          displayMaterial.uniforms.uDithering,
          ditheringTexture.attach(2)
        );
        let scale = getTextureScale(ditheringTexture, width, height);
        gl.uniform2f(displayMaterial.uniforms.ditherScale, scale.x, scale.y);
      }
      if (this.config.SUNRAYS)
        gl.uniform1i(displayMaterial.uniforms.uSunrays, sunrays.attach(3));

      blit(target);
    };

    const applyBloom = (source, destination) => {
      if (bloomFramebuffers.length < 2) return;

      let last = destination;

      gl.disable(gl.BLEND);
      bloomPrefilterProgram.bind();
      let knee =
        this.config.BLOOM_THRESHOLD * this.config.BLOOM_SOFT_KNEE + 0.0001;
      let curve0 = this.config.BLOOM_THRESHOLD - knee;
      let curve1 = knee * 2;
      let curve2 = 0.25 / knee;
      gl.uniform3f(
        bloomPrefilterProgram.uniforms.curve,
        curve0,
        curve1,
        curve2
      );
      gl.uniform1f(
        bloomPrefilterProgram.uniforms.threshold,
        this.config.BLOOM_THRESHOLD
      );
      gl.uniform1i(bloomPrefilterProgram.uniforms.uTexture, source.attach(0));
      blit(last);

      bloomBlurProgram.bind();
      for (let i = 0; i < bloomFramebuffers.length; i++) {
        let dest = bloomFramebuffers[i];
        gl.uniform2f(
          bloomBlurProgram.uniforms.texelSize,
          last.texelSizeX,
          last.texelSizeY
        );
        gl.uniform1i(bloomBlurProgram.uniforms.uTexture, last.attach(0));
        blit(dest);
        last = dest;
      }

      gl.blendFunc(gl.ONE, gl.ONE);
      gl.enable(gl.BLEND);

      for (let i = bloomFramebuffers.length - 2; i >= 0; i--) {
        let baseTex = bloomFramebuffers[i];
        gl.uniform2f(
          bloomBlurProgram.uniforms.texelSize,
          last.texelSizeX,
          last.texelSizeY
        );
        gl.uniform1i(bloomBlurProgram.uniforms.uTexture, last.attach(0));
        gl.viewport(0, 0, baseTex.width, baseTex.height);
        blit(baseTex);
        last = baseTex;
      }

      gl.disable(gl.BLEND);
      bloomFinalProgram.bind();
      gl.uniform2f(
        bloomFinalProgram.uniforms.texelSize,
        last.texelSizeX,
        last.texelSizeY
      );
      gl.uniform1i(bloomFinalProgram.uniforms.uTexture, last.attach(0));
      gl.uniform1f(
        bloomFinalProgram.uniforms.intensity,
        this.config.BLOOM_INTENSITY
      );
      blit(destination);
    };

    const applySunrays = (source, mask, destination) => {
      gl.disable(gl.BLEND);
      sunraysMaskProgram.bind();
      gl.uniform1i(sunraysMaskProgram.uniforms.uTexture, source.attach(0));
      blit(mask);

      sunraysProgram.bind();
      gl.uniform1f(sunraysProgram.uniforms.weight, this.config.SUNRAYS_WEIGHT);
      gl.uniform1i(sunraysProgram.uniforms.uTexture, mask.attach(0));
      blit(destination);
    };

    const blur = (target, temp, iterations) => {
      blurProgram.bind();
      for (let i = 0; i < iterations; i++) {
        gl.uniform2f(blurProgram.uniforms.texelSize, target.texelSizeX, 0.0);
        gl.uniform1i(blurProgram.uniforms.uTexture, target.attach(0));
        blit(temp);

        gl.uniform2f(blurProgram.uniforms.texelSize, 0.0, target.texelSizeY);
        gl.uniform1i(blurProgram.uniforms.uTexture, temp.attach(0));
        blit(target);
      }
    };

    const splatPointer = (pointer) => {
      let dx = pointer.deltaX * this.config.SPLAT_FORCE;
      let dy = pointer.deltaY * this.config.SPLAT_FORCE;
      splat(pointer.texcoordX, pointer.texcoordY, dx, dy, pointer.color);
    };

    const splat = (x, y, dx, dy, color, radius = this.config.SPLAT_RADIUS) => {
      splatProgram.bind();
      gl.uniform1i(splatProgram.uniforms.uTarget, this.velocity.read.attach(0));
      gl.uniform1f(
        splatProgram.uniforms.aspectRatio,
        this.el.width / this.el.height
      );
      gl.uniform2f(splatProgram.uniforms.point, x, y);
      gl.uniform3f(splatProgram.uniforms.color, dx, dy, 0.0);
      gl.uniform1f(splatProgram.uniforms.radius, correctRadius(radius / 100.0));
      blit(this.velocity.write);
      this.velocity.swap();

      gl.uniform1i(splatProgram.uniforms.uTarget, this.dye.read.attach(0));
      gl.uniform3f(splatProgram.uniforms.color, color.r, color.g, color.b);
      blit(this.dye.write);
      this.dye.swap();
    };

    const correctRadius = (radius) => {
      let aspectRatio = this.el.width / this.el.height;
      if (aspectRatio > 1) radius *= aspectRatio;
      return radius;
    };

    const getTextureScale = (texture, width, height) => {
      return {
        x: width / texture.width,
        y: height / texture.height,
      };
    };

    this.resizeCanvas();

    this.initFramebuffers();

    const color = this.generateColor(getRandomValue(0, 10));
    const dx = 20;
    const dy = 20;
    const r = getRandomValue(0.5, 1);

    const walker = (delta) => {
      for (let index = 0; index < 3; index++) {
        splat(
          (Math.sin(delta * 0.8 * 3000) + delta * 300) * r,
          Math.sin(delta * 0.3 * 2000) * r,
          dx,
          dy,
          color,
          this.config.SPLAT_RADIUS
        );
      }
    };

    requestAnimationFrame(update);

    // Offset listeners callback to avoid weird bug on immediate input
    this.inputListenersTimeout = window.setTimeout(() => {
      this.el.addEventListener("mousemove", this.onMouseMove);
    }, 1000);

    window.addEventListener("resize", this.onResize);
  }

  getResolution(resolution) {
    let aspectRatio = this.gl.drawingBufferWidth / this.gl.drawingBufferHeight;
    if (aspectRatio < 1) aspectRatio = 1.0 / aspectRatio;

    let min = Math.round(resolution);
    let max = Math.round(resolution * aspectRatio);

    if (this.gl.drawingBufferWidth > this.gl.drawingBufferHeight)
      return { width: max, height: min };
    else return { width: min, height: max };
  }

  initFramebuffers() {
    let simRes = this.getResolution(this.config.SIM_RESOLUTION);
    let dyeRes = this.getResolution(this.config.DYE_RESOLUTION);

    const texType = this.ext.halfFloatTexType;
    const rgba = this.ext.formatRGBA;
    const rg = this.ext.formatRG;
    const r = this.ext.formatR;
    const filtering = this.ext.supportLinearFiltering
      ? this.gl.LINEAR
      : this.gl.NEAREST;

    this.gl.disable(this.gl.BLEND);

    if (this.dye == null)
      this.dye = this.createDoubleFBO(
        dyeRes.width,
        dyeRes.height,
        rgba.internalFormat,
        rgba.format,
        texType,
        filtering
      );
    else
      this.dye = this.resizeDoubleFBO(
        this.dye,
        dyeRes.width,
        dyeRes.height,
        rgba.internalFormat,
        rgba.format,
        texType,
        filtering
      );

    if (this.velocity == null)
      this.velocity = this.createDoubleFBO(
        simRes.width,
        simRes.height,
        rg.internalFormat,
        rg.format,
        texType,
        filtering
      );
    else
      this.velocity = this.resizeDoubleFBO(
        this.velocity,
        simRes.width,
        simRes.height,
        rg.internalFormat,
        rg.format,
        texType,
        filtering
      );

    this.divergence = this.createFBO(
      simRes.width,
      simRes.height,
      r.internalFormat,
      r.format,
      texType,
      this.gl.NEAREST
    );
    this.curl = this.createFBO(
      simRes.width,
      simRes.height,
      r.internalFormat,
      r.format,
      texType,
      this.gl.NEAREST
    );
    this.pressure = this.createDoubleFBO(
      simRes.width,
      simRes.height,
      r.internalFormat,
      r.format,
      texType,
      this.gl.NEAREST
    );
    this.initBloomFramebuffers();
    this.initSunraysFramebuffers();
  }

  resizeCanvas() {
    let width = scaleByPixelRatio(this.el.clientWidth);
    let height = scaleByPixelRatio(this.el.clientHeight);

    if (this.el.width !== width || this.el.height !== height) {
      this.el.width = width;
      this.el.height = height;
      return true;
    }
    return false;
  }

  generateColor(delta) {
    const cos = (Math.sin(delta) + 1) / 2;
    const easing = easeInOutQuart(cos);
    const h = easing * (330 - 230) + 230;
    const s = easing * (1 - 0.66) + 0.66;
    const v = easing * (0.3 - 0.8) + 0.8;

    let c = HSVtoRGB(h / 360, s, v);

    c.r *= 0.15;
    c.g *= 0.15;
    c.b *= 0.15;

    return c;
  }

  updatePointerDownData(pointer, id, posX, posY) {
    pointer.id = id;
    pointer.down = true;
    pointer.moved = false;
    pointer.texcoordX = posX / this.el.width;
    pointer.texcoordY = 1.0 - posY / this.el.height;
    pointer.prevTexcoordX = pointer.texcoordX;
    pointer.prevTexcoordY = pointer.texcoordY;
    pointer.deltaX = 0;
    pointer.deltaY = 0;
    // pointer.color = this.generateColor(colorUpdateTimer);
  }

  updatePointerUpData(pointer) {
    pointer.down = false;
  }

  updatePointerMoveData(pointer, posX, posY) {
    pointer.prevTexcoordX = pointer.texcoordX;
    pointer.prevTexcoordY = pointer.texcoordY;
    pointer.texcoordX = posX / this.el.width;
    pointer.texcoordY = 1.0 - posY / this.el.height;
    // Fix a bug on Safari that triggers mouseMove 2 times
    const newDeltaX = this.correctDeltaX(
      pointer.texcoordX - pointer.prevTexcoordX
    );
    const newDeltaY = this.correctDeltaY(
      pointer.texcoordY - pointer.prevTexcoordY
    );
    pointer.deltaX = newDeltaX !== 0 ? newDeltaX : pointer.deltaX;
    pointer.deltaY = newDeltaY !== 0 ? newDeltaY : pointer.deltaY;

    pointer.moved =
      Math.abs(pointer.deltaX) > 0 || Math.abs(pointer.deltaY) > 0;
  }

  correctDeltaX(delta) {
    let aspectRatio = this.el.width / this.el.height;
    if (aspectRatio < 1) delta *= aspectRatio;
    return delta;
  }

  correctDeltaY(delta) {
    let aspectRatio = this.el.width / this.el.height;
    if (aspectRatio > 1) delta /= aspectRatio;
    return delta;
  }

  onMouseMove = (e) => {
    let posX = scaleByPixelRatio(e.offsetX);
    let posY = scaleByPixelRatio(e.offsetY);
    let pointer = this.pointers.find((p) => p.id === -1);
    if (pointer == null) pointer = new Pointer();
    this.updatePointerMoveData(pointer, posX, posY);
  };

  onResize = (e) => {
    if (this.resizeCanvas()) this.initFramebuffers();
  };

  pause() {
    this.paused = true;
  }

  play() {
    this.paused = false;
  }

  dismount() {
    if (this.inputListenersTimeout) {
      this.el.removeEventListener("mousemove", this.onMouseMove);
    } else {
      clearTimeout(this.inputListenersTimeout);
    }

    window.addEventListener("resize", this.onResize);
  }
}
